/* Media Queries for Responsiveness */
/* Large devices (desktops, 1200px and up) */
@media (min-width: 1200px) {
    .helloworld {
        font-size: 2rem;
        margin-top: 10%;
        margin-left: 8%;
        margin-right: 20%;
    }

    .kariemGerges {
        font-size: 5rem;
        margin-left: 8%;
        margin-right: 20%;
    }

    .Ibuild {
        font-size: 2.5rem;
        margin-left: 8%;
        margin-right: 20%;
    }

    .Iam {
        font-size: 1rem;
        margin-left: 8%;
        margin-right: 20%;
    }
}

/* Medium devices (tablets, 768px and up) */
@media (min-width: 768px) and (max-width: 1199px) {
    .helloworld {
        font-size: 1.5rem;
        margin-top: 10%;
        margin-left: 5%;
        margin-right: 10%;
    }

    .kariemGerges {
        font-size: 4rem;
        margin-left: 5%;
        margin-right: 10%;
    }

    .Ibuild {
        font-size: 2rem;
        margin-left: 5%;
        margin-right: 10%;
    }

    .Iam {
        font-size: 1rem;
        margin-left: 5%;
        margin-right: 10%;
    }
}

/* Small devices (phones, 767px and down) */
@media (max-width: 767px) {
    .helloworld {
        font-size: 1rem;
        margin-top: 20%;
        margin-left: 5%;
        margin-right: 5%;
    }

    .kariemGerges {
        font-size: 3rem;
        margin-left: 5%;
        margin-right: 5%;
    }

    .Ibuild {
        font-size: 1.5rem;
        margin-left: 5%;
        margin-right: 5%;
    }

    .Iam {
        font-size: 1rem;
        margin-left: 5%;
        margin-right: 5%;
    }
}

/* Default styles for desktop and larger screens */
.justNo-background {
    background-color: black;
    opacity: 0.8;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 2rem;
}

.helloworld, .kariemGerges, .Ibuild, .Iam {
    text-align: center;
    margin: 0.5rem;
}

.kariemGerges {
    font-size: 2rem;
    color: white;
}

.text-white {
    color: white;
}

@media only screen and (max-width: 1200px) {
    .kariemGerges {
        font-size: 1.8rem;
    }
    .container {
        padding: 1.5rem;
    }
}

@media only screen and (max-width: 992px) {
    .kariemGerges {
        font-size: 1.6rem;
    }
    .container {
        padding: 1rem;
    }
}

@media only screen and (max-width: 768px) {
    .kariemGerges {
        font-size: 1.4rem;
    }
    .container {
        flex-direction: column;
        padding: 0.5rem;
    }
    .helloworld, .Ibuild, .Iam {
        font-size: 1rem;
    }
}

@media only screen and (max-width: 576px) {
    .kariemGerges {
        font-size: 1.2rem;
    }
    .helloworld, .Ibuild, .Iam {
        font-size: 0.8rem;
    }
}



